.logo-mobile{            
    text-decoration: none;
    padding: 0px;
    margin: 0px;  
    color: rgba(255, 255, 255, 1);   
    font-family: Inter;
    font-size: 28px;
    font-weight: 400;
    line-height: 34px;
    letter-spacing: 0em;
    text-align: left;                            
 }

 .menu-container {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;       
    z-index: 1002;
    /* overflow: scroll; */
    color: rgba(255, 255, 255, 1); 
    transform: translateX(100%); 
    transition: transform 250ms ease-in-out;  
}  
.menu-container.is-open {
    transform: translateX(0);    
}
.mobile-menu {
    padding: 16px 20px 16px 21px;
    margin: 0; 
    width: 100%;      
    height: 100%; 
    background-image: url(../images/mobile.png);  
    background-size: cover;  
    background-repeat: no-repeat;
    background-position: center; 
    overflow: scroll;       
}

.mobile-menu__header{
    display: flex; 
    margin-bottom: 50px;   
}
.menu-toggle{ 
    padding: 0;  
    cursor: pointer; 
    margin-left: auto;      
    min-width: 32px;
    min-height: 32px;    
    background-color: rgba(7, 8, 28, 1);     
    color: rgba(255, 255, 255, 1);      
    border: none;
    border-radius: 50%; 
    outline: none; 
}

.nav-mobile{
    margin-bottom: 302px;    
}
.nav-mobile__list{  
    margin: 0;
    padding: 0;
    list-style-type: none;
    text-align: center;   
}
.nav-mobile__item{   
    margin-bottom: 16px; 
    height: 59px;   
    color: rgba(255, 255, 255, 1);           
}
.nav-mobile__item--about{ 
    margin-top: -59px;                           
}
.nav-mobile__link{ 
    margin: 0;
    padding: 0; 
    text-decoration: none;
    color: rgba(255, 255, 255, 1);            
    font-family: Inter;
    font-size: 32px;
    font-weight: 500;
    line-height: 1.218;
    letter-spacing: 0em;
    text-align: center;

    &:hover,
    &:focus{   
        background: linear-gradient(90.03deg, #00C5EB -1.97%, #B544FE 89.43%);
        background-clip: text;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;   
    }   
} 

.nav-mobile__link--about{   
    opacity: 0;  
    color:  rgba(0, 0, 0, 1);
    font-family: Montserrat;
    font-size: 32px; 
    font-weight: 700; 
    line-height: 1.218; 
    letter-spacing: 0em;  
    text-align: center;     
 }

.nav-mobile__link--current{  
    margin-bottom: 12px; 
